import { PoolClient } from "pg";
import z from "zod";
// Models
import FormSubmission, { FormDataT } from "@db/models/FormSubmission.js";
// Schema
import formSubmissionsSchema from "@schemas/form-submissions.js";
// Utils
import { SelectQueryBuilder } from "@utils/app/query-helpers.js";
import service from "@utils/app/service.js";
// Services
import formSubService from "@services/form-submissions/index.js";
import formsService from "@services/forms/index.js";
// Format
import formatFormSubmission from "@utils/format/format-form-submission.js";

export interface ServiceData {
  query: z.infer<typeof formSubmissionsSchema.getMultiple.query>;
  form_key: string;
  environment_key: string;
}

const getMultiple = async (client: PoolClient, data: ServiceData) => {
  // Check if form is assigned to environment
  await service(formSubService.hasEnvironmentPermission, false, client)(data);

  const { sort, include, page, per_page } = data.query;

  // Build Query Data and Query
  const SelectQuery = new SelectQueryBuilder({
    columns: [
      "id",
      "form_key",
      "environment_key",
      "read_at",
      "created_at",
      "updated_at",
    ],
    filter: {
      data: {
        environment_key: data.environment_key,
        form_key: data.form_key,
      },
      meta: {
        environment_key: {
          operator: "=",
          type: "text",
          columnType: "standard",
        },
        form_key: {
          operator: "=",
          type: "text",
          columnType: "standard",
        },
      },
    },
    sort: sort,
    page: page,
    per_page: per_page,
  });

  const formSubmissionsRes = await FormSubmission.getMultiple(
    client,
    SelectQuery
  );

  // Get Form Data
  const formBuilder = formsService.getBuilderInstance({
    form_key: data.form_key,
  });

  let formData: FormDataT[] = [];

  if (include?.includes("fields")) {
    const formSubmissionIds = formSubmissionsRes.data.map(
      (submission) => submission.id
    );
    formData = await FormSubmission.getAllFormData(client, {
      submission_ids: formSubmissionIds,
    });
  }

  const formattedSubmissions = formSubmissionsRes.data.map((submission) => {
    return formatFormSubmission(formBuilder, {
      submission,
      data: formData.filter(
        (field) => field.form_submission_id === submission.id
      ),
    });
  });

  return {
    data: formattedSubmissions,
    count: formSubmissionsRes.count,
  };
};

export default getMultiple;
